.comments-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  word-wrap: break-word;
  word-break: break-all;
  .comment-profile {
    display: flex;
    width: 100%;
    gap: 1rem;
    .comment-profile-name {
      display: flex;
      flex-direction: column;
      gap: 0.5rem;
      font-weight: bold;
      width: 100%;
      .comment-form {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .comment-textarea {
          font-family: "Roboto", sans-serif;
          font-size: 16px;
          width: 80%;
          padding: 8px 15px;
          background-color: #2a2b2c;
          color: #fff;
          border: 0;
          outline: 0;
          resize: none;
          border-radius: 50px;
          &::placeholder {
            letter-spacing: 0.25px;
          }
          @media (max-width: 700px) {
            width: 70%;
          }
        }
        .comment-publish-container {
          display: flex;
          width: 20%;
          display: flex;
          align-self: flex-start;
          justify-self: flex-start;
          @media (max-width: 700px) {
            width: 30%;
          }
          justify-content: center;
          .comment-publish {
            display: flex;
            font-family: "Roboto", sans-serif;
            border-radius: 20px;
            padding: 10px 15px;
            background-color: #2a2b2c;
            letter-spacing: 1px;
            outline: 0;
            border: 0;
            color: #fff;
            font-weight: bold;
            text-align: center;
            &:hover {
              cursor: pointer;
            }
          }
        }
      }
    }
  }
  .comments-label {
    font-weight: bold;
  }
  .comment {
    display: flex;
    flex-direction: column;
    .comment-inner-container {
      display: flex;
      flex-direction: column;
      gap: 0.5rem;
      padding-bottom: 1rem;
      .comment-picture-container {
        width: 100%;
        height: 48px;
        display: flex;
        gap: 1rem;
        border-radius: 50px;
        .comment-picture {
          width: 48px;
          border-radius: 50px;
          object-fit: cover;
        }
        .comment-name-date {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          width: 100%;
          .comment-name {
            font-weight: bold;
            height: 20px;
          }
          .comment-date {
            height: 20px;
            font-size: 13.33px;
          }
        }
      }
      .comment-content {
        text-align: left;
        padding: 5px 5px 0;
        word-wrap: break-word;
        word-break: break-all;
      }
    }

    .comment-interactions {
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding-bottom: 1rem;
      padding-left:  10px;
      padding-right:  10px;

      .comment-interactions-container {
        display: flex;
        gap: 1rem;

        .comment-interaction-container {
          .comment-interaction {
            font-family: "Roboto", sans-serif;
            color: #fff;
            background-color: #2a2b2c;
            border: 0;
            outline: 0;
            padding: 6px 10px;
            border-radius: 50px;
            letter-spacing: 0.25px;
            font-weight: bold;
            &:hover {
              cursor: pointer;
            }
          }
        }
      }

      border-bottom: 2px solid rgb(100, 100, 100);
    }
  }
}

.hidden {
  display: none;
}
